.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #0AF;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.header h1
{
  color: aliceblue;
}

.main_image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
 display: flex;
 justify-content: center;
 align-items: center;
}
/* .messi
{
  background-position: 1000px;
} */
.main-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}
